        padding-bottom: 30px
    }

    .about-us__content_row {
        grid-template-columns: 1.3fr 1fr
    }

    .about-us__reviews .reviews__title {
        padding-left: 230px
    }

    .about-us__reviews .splide__track {
        margin-left: max(245px,50% - 320px);
        margin-right: var(--container-padding)
    }

    .about-us__reviews .reviews__slide {
        min-width: 633px
    }

    .account {
        grid-template-columns: 299px calc(100% - 299px - 30px)
    }

    .account__title {
        grid-column: 2 span
    }

    .account__block_group {
        grid-template-columns: 1fr 1fr;
        gap: 20px 64px
    }

    .account__bonuses_list li {
        max-width: 410px
    }

    .account__bonuses_list li strong {
        font-size: 3.75rem
    }

    .not-found {
        display: grid;
        grid-template-columns: 1fr 1fr
    }

    .not-found__code {
        font-size: 15rem
    }

    .not-found__text {
        padding-top: 62px;
        margin: 0
    }

    .footer {
        grid-template-columns: 2fr 1fr 1fr;
        padding-bottom: 86px
    }

    .footer__logo {
        grid-column: 3 span
    }

    .footer__text {
        grid-column: auto;
        margin-bottom: 0
    }

    .footer__contacts {
        grid-column: 3;
        grid-row: 2 span
    }

    .footer__social {
        grid-column: 2;
        grid-row: 3;
        margin-top: 64px
    }

    .footer__copyright {
        margin-top: 65px;
        grid-column: 1;
        grid-row: 3
    }
}

@media (min-width:1200px) {
    .reviews__certificate_image {
        height: 285px
    }

    .reviews__certificate_image img {
        -webkit-transform: translate(-33%,-50%);
        transform: translate(-33%,-50%)
    }

    .product {
        grid-template-columns: 2.075fr 3fr
    }

    .product__info {
        grid-template-columns: 2fr minmax(250px,1.5fr)
    }

    .product__paymethods {
        display: block;
        padding: 30px
    }

    .product__paymethods li:not(:first-child) {
        padding-top: 8px
    }

    .product__gallery_nav.on-mob {
        display: none
    }

    .product__orders {
        margin-top: 30px
    }

    .cart__decor {
        display: flex
    }

    .about-us__reviews .splide__track {
        margin-right: 0
    }

    .about-us__reviews {
        -webkit-mask: linear-gradient(90deg,#000 80%,rgba(0,0,0,0) 90%,rgba(0,0,0,0) 100%)
    }

    .consultation__form {
        gap: 25px 45px
    }
}

@media (min-width:1290px) {
    .container {
        padding-left: max(var(--container-padding),50% - 645px);
        padding-right: max(var(--container-padding),50% - 645px)
    }

    .container.is-large {
        padding-left: max(var(--container-padding),50% - 860px);
        padding-right: max(var(--container-padding),50% - 860px)
    }
}

@media (min-width:1300px) {
    .header__logo--img {
        width: var(--width);
        height: var(--height)
    }

    .header__nav--list>li>a {
        font-size: .875rem
    }

    .index-hero__background--item {
        display: block
    }

    .catalog {
        grid-template-columns: 300px auto;
        gap: 40px 30px
    }

    .catalog__categories_list {
        padding: 20px 25px
    }
}

@media (min-width:1440px) {
    .about-us__wrapper {
        grid-template-columns: 299px calc(100% - 299px - 30px)
    }

    .about-us__reviews .reviews__title {
        padding-left: 329px
    }

    .about-us__reviews {
        -webkit-mask: linear-gradient(90deg,#000 60%,rgba(0,0,0,0) 90%,rgba(0,0,0,0) 100%)
    }

    .about-us__reviews .splide__track {
        margin-left: max(344px,50% - 320px)
    }
}

@media (min-width:1500px) {
    .statistics__background {
        width: calc(100% - 100px - 100px);
        left: 100px
    }

    .statistics__list {
        padding: 0
    }
}

@media (min-width:1550px) {
    .header__drop-down,.header__info,.header__message--text,.header__tel a {
        font-size: .875rem
    }

    .header__add-nav ul,.header__tel {
        gap: 30px
    }

    .header__nav--list>li>a {
        padding: 15px;
        font-size: 1rem
    }

    .header__add-nav a {
        width: 46px;
        height: 46px
    }
}

@media (min-width:1700px) {
    .cart__decor {
        opacity: .7
    }
}

@media (min-width:1720px) {
    .reviews__slide {
        padding: 35px 0 40px 50px
    }

    .reviews__certificate_image img {
        -webkit-transform: translate(-28%,-50%);
        transform: translate(-28%,-50%)
    }

    .reviews__certificate_link {
        margin-right: 70px
    }

    .about-us__decor img {
        opacity: 1
    }

    .account__block_footer {
        justify-content: space-between;
        grid-column: auto
    }

    .not-found__code {
        font-size: 18.75rem
    }
}

@media (max-width:1200px) {
    .product__gallery_nav.on-desktop {
        display: none
    }
}

@media (max-width:991px) {
    .visible-on-desktop {
        display: none!important
    }
}

@media screen and (-ms-high-contrast:none) {
    .splide.is-focus-in .splide__slide:focus,.splide__slide:focus-visible {
        border: 3px solid #0bf
    }

    .splide.is-focus-in .splide__track>.splide__list>.splide__slide:focus {
        border-color: #0bf
    }
}

@media (any-hover:hover) {
    .button:hover {
        background: var(--accent-hover)
    }

    .button:active,.header__drop-down--list a:hover {
        background: var(--accent-active)
    }

    .slider-pagination .splide__pagination__page:active::before,.slider-pagination .splide__pagination__page:hover::before {
        opacity: 1
    }

    .aside_nav a {
        transition: color .2s ease
    }

    .aside_nav a:hover {
        color: var(--accent-hover)
    }

    .aside_nav a:hover svg {
        color: var(--white)
    }

    .aside_nav a:active {
        color: var(--accent-active)
    }

    .aside_nav a:active svg,.password-toggle:hover {
        color: var(--white)
    }

    .aside_nav a svg {
        transition: background .2s ease
    }

    .header__tel a,.password-toggle {
        transition: color .2s ease
    }

    .password-toggle:active {
        color: var(--grey)
    }

    .header__tel a:hover {
        color: var(--accent-hover)
    }

    .header__tel a:active {
        color: var(--accent-active)
    }

    .header__drop-down:hover .header__drop-down--target svg:nth-child(3),.header__nav--list>li>a:hover {
        color: var(--accent-hover)
    }

    .header__drop-down:hover .header__drop-down--block {
        opacity: 1;
        visibility: visible
    }

    .header__drop-down--list a {
        transition: background .2s ease
    }

    .breadcrumbs__link,.header__nav--list>li>a,.pagination__link,.pagination__list a,.popup-text a {
        transition: color .2s ease
    }

    .header__add-nav a:hover,.header__drop-down--list a:active {
        background: var(--accent-hover)
    }

    .header__nav--list>li>a:active {
        color: var(--accent-active)
    }

    .header__add-nav a {
        transition: background .2s ease,color .2s ease
    }

    .header__add-nav a:hover {
        color: var(--white)
    }

    .header__add-nav a:active {
        background: var(--accent-active)
    }

    .breadcrumbs__link:hover {
        color: var(--accent-hover)
    }

    .breadcrumbs__link:active {
        color: var(--accent-active)
    }

    .pagination__link:hover {
        color: var(--accent-hover)
    }

    .pagination__link:active {
        color: var(--grey)
    }

    .pagination__list a:not(.is-current):hover {
        color: var(--accent-hover)
    }

    .pagination__list a:not(.is-current):active {
        color: var(--grey)
    }

    .popup-text a:hover {
        color: var(--accent-hover)
    }

    .popup-text a:active {
        color: var(--accent-active)
    }

    .popup-close-btn {
        transition: background .2s ease
    }

    .popup-close-btn:hover {
        background: rgba(35,28,49,.5)
    }

    .popup-close-btn:active {
        background: rgba(35,28,49,.7)
    }

    .popup-form__text a {
        transition: color .2s ease
    }

    .popup-form__text a:hover {
        color: var(--accent-hover)
    }

    .popup-form__text a:active {
        color: var(--accent-active)
    }

    .index-hero__search--form button {
        transition: background .2s ease
    }

    .categories__list li a::before,.product-card__image::before {
        content: "";
        left: 50%;
        top: 50%;
        transition: opacity .3s ease,visibility .3s ease
    }

    .index-hero__search--form button:hover {
        background: var(--accent-hover)
    }

    .index-hero__search--form button:active {
        background: var(--accent)
    }

    .categories__list li a::before {
        position: absolute;
        -webkit-transform: translate(-50%,-45%);
        transform: translate(-50%,-45%);
        width: 200px;
        height: 200px;
        border-radius: 50%;
        background: var(--accent-circle-gradient);
        z-index: -1;
        opacity: 0;
        visibility: hidden
    }

    .categories__list li a:hover::before {
        opacity: 1;
        visibility: visible
    }

    .categories__list li a:active::before {
        opacity: .7;
        visibility: visible
    }

    .product-card__image::before {
        position: absolute;
        width: 140%;
        height: 140%;
        -webkit-transform: translate(-50%,-50%);
        transform: translate(-50%,-50%);
        background: var(--accent-circle-gradient);
        pointer-events: none;
        opacity: 0;
        visibility: hidden
    }

    .product-card__title a,.reviews__certificate_link {
        transition: color .2s ease
    }

    .product-card__image:hover::before {
        opacity: 1;
        visibility: visible
    }

    .product-card__title a:hover {
        color: var(--accent-hover)
    }

    .product-card__title a:active {
        color: var(--accent-active)
    }

    .catalog__search button,.product__length button {
        transition: background .2s ease
    }

    .reviews__certificate_link:hover {
        color: var(--accent-hover)
    }

    .reviews__certificate_link:active {
        color: var(--accent-active)
    }

    .catalog__categories_list a:hover i {
        background: rgba(217,217,217,.2)
    }

    .catalog__categories_list a:active i,.catalog__search button:hover {
        background: var(--accent-hover)
    }

    .catalog__search button:active {
        background: var(--accent)
    }

    .product__length button:hover {
        background: var(--accent-hover)
    }

    .product__length button:active {
        background: var(--accent-active)
    }

    .product__gallery_nav_slide:not(.is-active):hover {
        opacity: .8
    }

    .product__gallery_nav_slide:not(.is-active):active {
        opacity: .7
    }

    .cart__note a {
        transition: color .2s ease
    }

    .cart__note a:hover {
        color: var(--accent-hover)
    }

    .cart__note a:active {
        color: var(--grey)
    }

    .cart__item_remove {
        transition: background .2s ease
    }

    .cart__item_remove:hover {
        background: var(--accent-hover)
    }

    .cart__item_remove:active {
        background: var(--accent-active)
    }

    .orders__card_body:hover .orders__card_number strong {
        color: var(--accent-hover);
        -webkit-text-stroke-color: var(--accent-hover)
    }

    .orders__card_body:hover .orders__card_link_icon {
        color: var(--accent-hover)
    }

    .orders__card_body:active .orders__card_number strong {
        color: var(--accent-active);
        -webkit-text-stroke-color: var(--accent-active)
    }

    .orders__card_body:active .orders__card_link_icon {
        color: var(--accent-active)
    }

    .orders__card_number strong {
        transition: color .3s ease,-webkit-text-stroke-color .3s ease
    }

    .orders__card_link_icon {
        transition: color .3s ease
    }

    .about-us__aside_nav a,.pricelist__item i {
        transition: background .2s ease
    }

    .about-us__aside_nav a:hover {
        background: #554275
    }

    .about-us__aside_nav a:active {
        background: #4b376d
    }

    .pricelist__item:hover i {
        background: var(--accent-hover)
    }

    .pricelist__item:active i {
        background: var(--accent-active)
    }

    .footer__contacts a,.footer__social a,.footer__tel a {
        transition: color .2s ease
    }

    .account__block_edit:hover {
        background: var(--white);
        color: var(--accent)
    }

    .account__block_edit:active {
        background: var(--grey);
        color: var(--accent)
    }

    .footer__contacts a:hover,.footer__social a:hover,.footer__tel a:hover {
        color: var(--accent-hover)
    }
}